<script setup lang="ts">
import type { PropType } from 'vue'
import type { PageConfig } from '@/types'
import { ref } from 'vue'
import { Target, GraduationCap, Layout, Code, FileText, Book, ShoppingCart, CreditCard, Link as LinkIcon, HelpCircle, Award } from 'lucide-vue-next'

const props = defineProps({
  config: {
    type: Object as PropType<PageConfig>,
    required: true
  }
})

const selectedCards = ref<number[]>([])

const toggleSelection = (index: number) => {
  if (selectedCards.value.includes(index)) {
    selectedCards.value = selectedCards.value.filter(i => i !== index)
  } else {
    selectedCards.value.push(index)
  }
}

const icons: Record<string, any> = {
  target: Target,
  gradCap: GraduationCap,
  layout: Layout,
  code: Code,
  fileText: FileText,
  book: Book,
  cart: ShoppingCart,
  card: CreditCard,
  link: LinkIcon,
  help: HelpCircle,
  award: Award
}
</script>

<template>
  <div class="h-full w-full flex flex-col items-center justify-center bg-slate-50 p-8">
    <div class="max-w-5xl w-full h-full flex flex-col">
      <div class="mb-8 text-center shrink-0">
        <h2 class="text-3xl font-bold text-slate-900 mb-2">{{ config.title }}</h2>
        <p v-if="config.subTitle" class="text-slate-500">{{ config.subTitle }}</p>
      </div>

      <div class="flex-1 min-h-0 overflow-y-auto">
        <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 pb-4">
          <div 
            v-for="(card, index) in config.cards" 
            :key="index"
            class="group bg-white rounded-xl p-6 shadow-sm border transition-all duration-300 flex flex-col relative overflow-hidden"
            :class="[
              card.variant === 'wide' ? 'md:col-span-2 lg:col-span-3 flex-row items-center gap-6' : '',
              card.selectable ? 'cursor-pointer' : '',
              card.selectable && selectedCards.includes(index) ? 'border-blue-500 ring-2 ring-blue-500/20 bg-blue-50/10' : 'border-slate-100 hover:shadow-md hover:border-slate-200'
            ]"
            @click="card.selectable ? toggleSelection(index) : null"
          >
            <!-- Selectable Highlight Overlay -->
            <div v-if="card.selectable" class="absolute inset-0 bg-blue-500/0 transition-colors duration-200 pointer-events-none" :class="{ 'group-hover:bg-blue-500/[0.02]': !selectedCards.includes(index) }"></div>

            <!-- Icon -->
            <div 
              class="w-12 h-12 rounded-lg flex items-center justify-center mb-4 shrink-0 transition-colors"
              :class="[
                card.variant === 'wide' ? 'mb-0' : '',
                card.selectable && selectedCards.includes(index) ? 'bg-blue-100 text-blue-600' : 'bg-slate-50 text-slate-600 group-hover:bg-blue-50 group-hover:text-blue-600'
              ]"
            >
              <component 
                v-if="card.icon && icons[card.icon]" 
                :is="icons[card.icon]" 
                class="w-6 h-6" 
              />
            </div>

            <!-- Content -->
            <div class="flex-1 relative z-10">
              <h3 class="font-bold text-lg text-slate-900 mb-2">{{ card.title }}</h3>
              <p class="text-slate-500 text-sm leading-relaxed mb-4">{{ card.subTitle }}</p>
              
              <!-- Features list for selectable cards -->
              <ul v-if="card.features" class="space-y-2 mb-4">
                <li v-for="(feature, idx) in card.features" :key="idx" class="flex items-center text-xs text-slate-600">
                  <span class="w-1.5 h-1.5 rounded-full bg-blue-400 mr-2"></span>
                  {{ feature }}
                </li>
              </ul>

              <!-- Link -->
              <a 
                v-if="card.link" 
                :href="card.link.url" 
                target="_blank" 
                class="inline-flex items-center text-sm font-medium text-blue-600 hover:text-blue-700 mt-auto"
                @click.stop
              >
                {{ card.link.text }}
                <LinkIcon class="w-3 h-3 ml-1" />
              </a>
            </div>

            <!-- Selection Checkmark -->
            <div v-if="card.selectable" class="absolute top-4 right-4 transition-all duration-300" :class="selectedCards.includes(index) ? 'opacity-100 scale-100' : 'opacity-0 scale-50'">
              <div class="w-6 h-6 rounded-full bg-blue-500 text-white flex items-center justify-center shadow-sm">
                <svg class="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="3" d="M5 13l4 4L19 7"></path></svg>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>